.pa-foot-box ul li {
    margin-bottom: 10px;
    list-style: none;
    display: list-item;
}

.pa-foot-box p, .pa-foot-box a {
    color: #fff;
    transition: 0.3s;
}
.pa-newsletter {
    position: relative;
    margin-bottom: 25px;
}
form {
    display: block;
    margin-top: 0em;
}
.pa-newsletter input {
    padding-right: 170px;
    border: none;
}
input {
    width: 100%;
    height: 50px;
    padding: 0px 20px;
    border: 1px solid #6caaa8;
    outline: none;
    border-radius: 50px;
}
.pa-btn {
    background: #6caaa8;
    color: #fff;
    padding: 0px 20px;
    min-width: 160px;
    height: 50px;
    line-height: 46px;
    border: none;
    border-radius: 50px;
    text-transform: capitalize;
    outline: none;
    text-align: center;
    cursor: pointer;
    border: 2px solid #6caaa8;
    transition: 0.3s;
    display: inline-block;
}
.pa-newsletter .pa-btn {
    position: absolute;
    right: 5px;
    top: 5px;
    height: 40px;
    line-height: 36px;
}
.pa-foot-box ul li:last-child, .pa-foot-box p {
    margin-bottom: 0px;
}
.pa-copyright.pa-copyright-two {
    background: #589a98;
}
.pa-copyright {
    /* background: #6caaa8; */
    padding: 12px 0;
    text-align: center;
}
.pa-copyright p, .pa-copyright p a {
    margin: 0;
    color: #fff;
    transition: 0.3s;
}
@media screen and (max-width: 767px){
    .pa-footer, .pa-footer-two, .pa-footer-three {
        padding: 52px 0 23px;
    }
}
@media screen and (max-width: 767px){
    .pa-foot-title, .pa-foot-box img {
        margin-bottom: 20px;
    }
}
@media screen and (max-width: 767px){
    .pa-foot-title, .pa-foot-box img {
        margin-bottom: 20px;
    }
}


.pa-why, .pa-counter, .pa-tesimonial, .pa-why-pure, .pa-team-home {
    background: rgba(108, 170, 168, 0.1);
}
.pa-heading {
    text-align: center;
    margin-bottom: 46px;
}
div {
    display: block;
}
.pa-heading img {
    opacity: 0.4;
}
.pa-heading h1 {
    margin-bottom: 15px;
    text-transform: capitalize;
    font-size: 32px;
}
.pa-heading h5 {
    margin-bottom: 0;
    text-transform: capitalize;
    font-size: 20px;
    color: #6caaa8;
}
/* main tag */
ul {
    margin: 0;
    padding: 0;
}
/* left elements */
.pa-why-left ul li:first-child {
    padding-right: 85px;
}
.pa-why-left ul li {
    text-align: right;
    padding-right: 150px;
}
.pa-why-ul ul li {
    font-size: 20px;
    margin-bottom: 60px;
    font-weight: 500;
    position: relative;
    text-transform: capitalize;
}
li {
    list-style: none;
}
.pa-why-left ul li:nth-child(2) {
    padding-right: 140px;
}
.pa-why-left ul li {
    text-align: right;
    padding-right: 150px;
}
.pa-why-left ul li:nth-child(4) {
    padding-right: 145px;
}
.pa-why-left ul li:last-child {
    padding-right: 100px;
}
.pa-why-ul ul li:last-child {
    margin-bottom: 0;
}
.pa-why-img {
    width: 380px;
    height: 380px;
    line-height: 380px;
    border: 1px dashed #6caaa8;
    border-radius: 100%;
    text-align: center;
    background-image:  url("/images/Sukhayu logo english.png");
    /* background-color: #b1ecea; */
}
.pa-why-img img {
    margin-top: -165px;
    width: 322px;
}

/* right li */
.pa-why-right ul li:first-child {
    padding-left: 95px;
}
.pa-why-right ul li {
    text-align: left;
    padding-left: 150px;
}
.pa-why-right ul li:nth-child(2) {
    padding-left: 140px;
}
