 #no-freeze-spinner {
	 position: fixed;
	 top: 0;
	 left: 0;
	 width: 100%;
	 transition: all 0.1s linear;
	 opacity: 0;
	 height: 80%;
	 -webkit-transform: scale(10);
	         transform: scale(10);
	 z-index: -1;
	 background: rgba(0, 0, 0, 0.0);
}
 .no-freeze-spinner #no-freeze-spinner {
	 z-index: 9999;
	 opacity: 0.5;
	 -webkit-transform: scale(1);
	         transform: scale(1);
}
 #no-freeze-spinner > div > div {
	 -webkit-animation-play-state: paused;
	         animation-play-state: paused;
	 border: 5px solid rgba(200, 200, 200, 1);
	 border-radius: 50%;
	 -webkit-animation: rotate 1s infinite linear;
	         animation: rotate 1s infinite linear;
	 border-left-color: rgba(200, 200, 200, 0.4);
	 width: 100%;
	 height: 100%;
}
 #no-freeze-spinner > div {
	 position: absolute;
	 top: 0;
	 left: 0;
	 right: 0;
	 bottom: 0;
	 margin: auto;
	 border-radius: 50%;
	 box-shadow: 0 0 0 2000px rgba(0, 0, 0, 0.0);
	 width: 60px;
	 height: 60px;
	 padding: 5px;
}
 .no-freeze-spinner #no-freeze-spinner div div {
	 -webkit-animation-play-state: running;
	         animation-play-state: running;
}
 @-webkit-keyframes loadicons {
	 0% {
		 -webkit-transform: scale(0) translate3d(0, 0, 0);
		         transform: scale(0) translate3d(0, 0, 0);
	}
	 11% {
		 -webkit-transform: scale(1.2) translate3d(0, 0, 0);
		         transform: scale(1.2) translate3d(0, 0, 0);
	}
	 22% {
		 -webkit-transform: scale(1) translate3d(0, 0, 0);
		         transform: scale(1) translate3d(0, 0, 0);
	}
	 33% {
		 -webkit-transform: scale(0) translate3d(0, 0, 0);
		         transform: scale(0) translate3d(0, 0, 0);
	}
}
 @keyframes loadicons {
	 0% {
		 -webkit-transform: scale(0) translate3d(0, 0, 0);
		         transform: scale(0) translate3d(0, 0, 0);
	}
	 11% {
		 -webkit-transform: scale(1.2) translate3d(0, 0, 0);
		         transform: scale(1.2) translate3d(0, 0, 0);
	}
	 22% {
		 -webkit-transform: scale(1) translate3d(0, 0, 0);
		         transform: scale(1) translate3d(0, 0, 0);
	}
	 33% {
		 -webkit-transform: scale(0) translate3d(0, 0, 0);
		         transform: scale(0) translate3d(0, 0, 0);
	}
}
 @-webkit-keyframes rotate {
	 100% {
		 -webkit-transform: rotate(360deg);
		         transform: rotate(360deg);
	}
}
 @keyframes rotate {
	 100% {
		 -webkit-transform: rotate(360deg);
		         transform: rotate(360deg);
	}
}
 .no-freeze-spinner #no-freeze-spinner i{
	 font-size: 24px;
	 position: absolute;
	 top: 0;
	 left: 0;
	 right: 0;
	 bottom: 0;
	 margin: auto;
	 color: black;
	 width: 24px;
	 height: 24px;
	 line-height: 1;
	 -webkit-transform: scale(0) translate3d(0, 0, 0);
	         transform: scale(0) translate3d(0, 0, 0);
}
 .no-freeze-spinner #no-freeze-spinner i:nth-of-type(1) {
	 -webkit-animation: loadicons 3s infinite ease-in-out;
	         animation: loadicons 3s infinite ease-in-out;
}
 .no-freeze-spinner #no-freeze-spinner i:nth-of-type(2) {
	 -webkit-animation: loadicons 3s 1s infinite ease-in-out;
	         animation: loadicons 3s 1s infinite ease-in-out;
}
 .no-freeze-spinner #no-freeze-spinner i:nth-of-type(3) {
	 -webkit-animation: loadicons 3s 2s infinite ease-in-out;
	         animation: loadicons 3s 2s infinite ease-in-out;
}
 
