  content: "\f10d";
  font-family: fontawesome;
  position: absolute;
  top: 0;
  left: 20px;
  font-size: 20px;
  color: #D0AB6D;
}
.testimonial .description:after{
  content: "\f10e";
  font-family: fontawesome;
  font-size: 20px;
