.Auth-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    /* width: 100vw; */
    height: 100vh;
    /* background: linear-gradient(-45deg, #74ebd5, #9face6, #fcd3a5);
    background-size: 400% 400%;
    animation: wave 5s ease-in-out infinite; */
  }
  
  .Auth-form-wrapper {
    width: 420px;
    padding-top: 30px;
    padding-bottom: 20px;
  }
  
  .Auth-form {
    width: 420px;
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
    padding-top: 30px;
    padding-bottom: 20px;
    border-radius: 8px;
    background-color: white;
  }
  
  .Auth-form-content {
    padding-left: 12%;
    padding-right: 12%;
  }
  
  .Auth-form-title {
    text-align: center;
    margin-bottom: 1em;
    font-size: 24px;
    color: rgb(34, 34, 34);
    font-weight: 800;
  }
  
  .authlabel {
    font-size: 14px;
    font-weight: 600;
    color: rgb(34, 34, 34);
  }



  @keyframes wave {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  
    